export default {
  getExercises: state => state.exercises,
  getPrograms: state => state.programs,
  getCurrentExerciseID: state => state.currentExerciseID,
  getCurrentProgramID: state => state.currentProgramID,
  getCurrentExercise: state => state.currentExercise,
  getCurrentProgram: state => state.currentProgram,
  getRunningProgram: state => state.runningProgram,
  getRunningProgramData: state => state.runningProgramData,
  shownPopupPrograms : state => state.popupPrograms,
  getProgramDataById : (state) => (id) => {
    if(state.programs && state.programs.length>0) {
      return state.programs.find((item)=>item.id = id)
    }
    return null
  }
}
