export default {
  getJoinedPrograms: state => state.joins,
  getJoinedProgramsSorted: state => {
    return Object.values(state.joins).sort($nuxt.$exerciseManager.compareJoinedPrograms)
  },
  getJoinedProgramById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      return state.joins[id]
    }
    return null
  },
  hasJoinedPrograms: state => {
    return Object.keys(state.joins).length > 0
  },
  hasJoinedProgramById: state => (id) => {
    return (typeof state.joins[id] !== 'undefined') ? true : false
  },
  getProgramById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      return state.joins[id].program
    }
    return null
  },
  getProgramJoinedAtById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      return $nuxt.$exerciseManager.resetTimeToStartOfDay(state.joins[id].joined_at)
    }
    return null
  },
  getDoneCountByProgramId: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      const program = state.joins[id].program
      return program.exercises.filter((i) => i.completed === true).length;
    }
    return 0;
  },
  getDoneCountByProgramData: (state) => (data) => {
    if (typeof data !== 'undefined') {
      return data.exercises.filter((i) => i.completed === true).length;
    }
    return 0;
  },
  getProgramDayTags: (state) => (id) => {
    const program = state.joins[id].program
    if (program) {
      let tags = {};
      program.exercises.map((item) => {
        item.tags.map((tag) => {
          if (typeof tag['kind'] !== 'undefined' && tag.kind === 'temporal') {
            tags[tag._id] = tag;
          }
        })
      });
      return Object.values(tags).sort($nuxt.$exerciseManager.compareTemporalTag);
    }
    return null
  },
  hasProgramDayTags: (state) => (id) => {
    const _tags = $nuxt.$store.getters.getProgramDayTags(id);
    if (_tags) {
      return _tags.length > 0
    }
    return false;
  },
  getNextExerciseByProgramId: (state) => (id, currentExercise) => {
    if (typeof state.joins[id] !== 'undefined') {
      const program = $nuxt.$store.getters.getProgramById(id)
      let joinTime = $nuxt.$store.getters.getProgramJoinedAtById(id)
      if (!joinTime) {
        joinTime = $nuxt.$exerciseManager.resetTimeToStartOfDay((new Date()).getTime())
      }
      let isCurrentDay = true;
      let isTemporalProgram = false;
      let currentTagExercise = true;
      console.log('compute next exercise by Program', currentExercise.tags, id, new Date(joinTime))

      if ($nuxt.$store.getters.hasProgramDayTags(id)) {
        console.log('This program is with Temporal Tags')
        isCurrentDay = false;
        const today = $nuxt.$exerciseManager.resetTimeToStartOfDay(new Date().getTime())
        const tTags = currentExercise.tags.filter((item) => item.kind === 'temporal');
        if (tTags.length === 0) {
          isCurrentDay = true
        } else {
          isTemporalProgram = true;
          const currentPTag = tTags[0];

          currentTagExercise = currentPTag;
          let intervalMillis = 86400 * 1000; //Day
          if (currentPTag.unitKind == 'week') {
            intervalMillis = intervalMillis * 7
          } else if (currentPTag.unitKind == 'month') {
            intervalMillis = intervalMillis * 30
          }
          const exerciseInterval = $nuxt.$exerciseManager.computeTagInterval(tTags[0]);
          console.log('Exercise Tag', currentPTag, exerciseInterval, intervalMillis)
          const startExerciseTime = joinTime + exerciseInterval
          console.log('Exercise tag check:', startExerciseTime, today)
          if (today >= startExerciseTime) {
            isCurrentDay = true
          }
        }
      }
      if (isCurrentDay === false) {
        return null
      }


      let nextExercise = null;
      const firstNotCompleted =
        isTemporalProgram && currentTagExercise != true ?
          program.exercises.find((i) => i.completed === false && i.tags.filter((t) => t._id == currentTagExercise._id).length > 0) :
          program.exercises.find((i) => i.completed === false);
      if (firstNotCompleted) {
        nextExercise = firstNotCompleted;
      } else if (isTemporalProgram && currentTagExercise != true) {
        if (program.exercises.filter((i) => i.completed !== true && i.tags.filter((t) => t._id == currentTagExercise._id).length > 0).length < 1) {
          nextExercise = null;
        } else {
          nextExercise = program.exercises.filter((i) => i.tags.filter((t) => t._id == currentTagExercise._id).length > 0)[0];
        }
      } else {
        if (program.exercises.filter((i) => i.completed !== true).length < 1) {
          nextExercise = null;
        } else {
          nextExercise = program.exercises[0];
        }
      }
      if (nextExercise !== null) {
        nextExercise.program_name = program.title
      }
      return nextExercise
    }
    return null
  },
  getNextExerciseByProgramData: (state) => (program, currentExercise) => {
    const id = program._id||program.id
    if (typeof state.joins[id] !== 'undefined') {
      let joinTime = $nuxt.$store.getters.getProgramJoinedAtById(id)
      if (!joinTime) {
        joinTime = $nuxt.$exerciseManager.resetTimeToStartOfDay((new Date()).getTime())
      }
      let isCurrentDay = true;
      let isTemporalProgram = false;
      let currentTagExercise = true;
      console.log('compute next exercise by Program', currentExercise.tags, id, new Date(joinTime))

      if ($nuxt.$store.getters.hasProgramDayTags(id)) {
        console.log('This program is with Temporal Tags')
        isCurrentDay = false;
        const today = $nuxt.$exerciseManager.resetTimeToStartOfDay(new Date().getTime())
        const tTags = currentExercise.tags.filter((item) => item.kind === 'temporal');
        if (tTags.length === 0) {
          isCurrentDay = true
        } else {
          isTemporalProgram = true;
          const currentPTag = tTags[0];

          currentTagExercise = currentPTag;
          let intervalMillis = 86400 * 1000; //Day
          if (currentPTag.unitKind == 'week') {
            intervalMillis = intervalMillis * 7
          } else if (currentPTag.unitKind == 'month') {
            intervalMillis = intervalMillis * 30
          }
          const exerciseInterval = $nuxt.$exerciseManager.computeTagInterval(tTags[0]);
          console.log('Exercise Tag', currentPTag, exerciseInterval, intervalMillis)
          const startExerciseTime = joinTime + exerciseInterval
          console.log('Exercise tag check:', startExerciseTime, today)
          if (today >= startExerciseTime) {
            isCurrentDay = true
          }
        }
      }
      if (isCurrentDay === false) {
        return null
      }


      let nextExercise = null;
      const firstNotCompleted =
        isTemporalProgram && currentTagExercise != true ?
          program.exercises.find((i) => i.completed === false && i.tags.filter((t) => t._id == currentTagExercise._id).length > 0) :
          program.exercises.find((i) => i.completed === false);
      if (firstNotCompleted) {
        nextExercise = firstNotCompleted;
      } else if (isTemporalProgram && currentTagExercise != true) {
        if (program.exercises.filter((i) => i.completed !== true && i.tags.filter((t) => t._id == currentTagExercise._id).length > 0).length < 1) {
          nextExercise = null;
        } else {
          nextExercise = program.exercises.filter((i) => i.tags.filter((t) => t._id == currentTagExercise._id).length > 0)[0];
        }
      } else {
        if (program.exercises.filter((i) => i.completed !== true).length < 1) {
          nextExercise = null;
        } else {
          nextExercise = program.exercises[0];
        }
      }
      if (nextExercise !== null) {
        nextExercise.program_name = program.title
      }
      return nextExercise
    }
    return null
  },
  getOverallProgressProgramById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      const program = state.joins[id].program
      return Math.round(
        (program.exercises.filter((i) => i.completed === true).length / program.exercises.length) * 100
      );
    }
    return 0;
  },
  getExercisetocompleteCountProgramById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      const program = state.joins[id].program
      return program.exercises.reduce((carry, item) => {
        return carry + (item.completed ? 0 : 1);
      }, 0);
    }
    return 0;
  },
  getExercisecompletedCountProgramById: (state) => (id) => {
    if (typeof state.joins[id] !== 'undefined') {
      const program = state.joins[id].program
      return program.exercises.reduce((carry, item) => {
        return carry + (item.completed ? 1 : 0);
      }, 0);
    }
    return 0;
  },
  hasShownPopupByIdAndTag: (state) => (id, tagId) => {
    if( state.shownPopups.filter(v=>v===id+"_"+tagId).length>0) {
      return true;
    }
    return false;
  },
  hasShownPopupById: (state) => (id) => {
    if( state.shownPopups.filter(v=>v===id+"_WHOLEPROGRAM").length>0) {
      return true;
    }
    return false;
  }
}
