

const _cogImage = require("~/assets/images/cog_image.png");
const _folderImage = require("~/assets/images/folder_image.png");
const _micImage = require("~/assets/images/mic_image.png");
const _permImage = require("~/assets/images/permission_image.png");

export default {
  props: {
    show: {
      type: Boolean,
      default: false
    },
    warning: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    showDialog: function () {
      return this.show;
    },
    micImage() {
      return _micImage;
    },
    folderImage() {
      return _folderImage;
    },
    cogImage() {
      return _cogImage;
    },
    primaryImage() {
      return _permImage;
    }
  },
  methods: {
    closeClick(e) {
      this.$emit('close', e);
    },
    nextClick(e) {
      this.$emit('next', e);
    }

  },
  mounted() {

  }
}
