export default {
  SET_TUTORIALRUN: (state, payload) => {
    state.tutorials[payload.page+"_"+payload.index] = true
    state.tutorials[payload.page] = true
    state.updateTime = new Date().getTime();
  },
  SET_CLEARTUTORIAL: (state, payload) => {
    state.tutorials = {}
    state.updateTime = new Date().getTime();
  }
}
