export default {
  addJoinedProgram: ({commit}, payload) => {
    commit('ADD_PROGRAM', payload)
  },
  setJoinedProgram: ({commit}, payload) => {
    commit('SET_PROGRAM', payload)
  },
  leaveJoinedProgram: ({commit}, payload) => {
    commit('DEL_PROGRAM', payload)
  },
  clearAllJoinedProgram: ({commit}, payload) => {
    commit('CLEAR_ALL', payload)
  },
  setJoinedProgramExercise: ({commit}, payload) => {
    commit('UPDATE_EXERCISE_PROGRAM', payload)
  },
  shownPopup:({commit}, payload) => {
    commit('SET_SHOWNPOPUP', payload)
  },
  clearAllShownPopup:({commit}, payload) => {
    commit('CLEAR_ALL_SHOWN', payload)
  },
}
