export default {
  SET_VIDEODEVICES: (state, payload) => {
    state.videodevices = payload
  },
  SET_AUDIODEVICES: (state, payload) => {
    state.audiodevices = payload
  },
  CLEAR_MEDIADEVICES: (state, payload) => {
    state.audiodevices = [];
    state.videodevices = [];
  },
  CLEAR_AUDIODEVICES: (state, payload) => {
    state.audiodevices = [];
  },
  CLEAR_VIDEODEVICES: (state, payload) => {
    state.videodevices = [];
  },
  SET_HOMESCROLL: (state, payload) => {
    state.homeScroll = payload
  },
  SET_ORISCROLL: (state, payload) => {
    if (payload.who === 'mychallenges') {
      state.oriMyChallengeScroll = payload.scroll;
    } else if (payload.who === 'exercises') {
      state.oriExerciseScroll = payload.scroll;
    } else if (payload.who === 'challenges') {
      state.oriChallengeScroll = payload.scroll;
    }
  },

}
