export default {
  getCurrentUser: state => state.currentuser,
  isCurrentUserVerified: state => state.currentuser && state.currentuser.verified,
  isCurrentUserGuest: state => {
    if (state.isguest) {
      return state.isguest
    }
    if (state.currentuser.email === 'airlyn-guest@voicemed.io') {
      return true
    }
    return false
  },
  performedExercises: state => state.performed,
  skipTutorial: state => state.skiptutorial,
  doneExercices: state => state.sessionCounter,
  isNotificationsEnabled: state => state.notification.enabled,
  notificationPreferredTime: state => state.notification.preferredtime
}
