export default {
  ADD_PROGRAM: (state, payload) => {
    const _localProgram = payload.localprogram
    const _remoteProgram = typeof payload.remoteprogram !== 'undefined' ? payload.remoteprogram : null
    let joinTime = new Date().getTime();
    let completedTime = null;
    let completed = false;
    if (_remoteProgram) {
      joinTime = new Date(_remoteProgram.joinedAt).getTime()
      completedTime = new Date(_remoteProgram.completedAt).getTime()
      completed = _remoteProgram.completed
    }

    if (typeof state.joins[_localProgram.id] === 'undefined') {
      _localProgram.completed = completed;
      //Compute completed for program:
      const program = _localProgram
      const exerciseCount = program.exercises.length;
      const toComplete = program.exercises.reduce((carry, item) => {
        return carry + (item.completed ? 0 : 1);
      }, 0);
      if(typeof _localProgram.lastactivity_at == 'undefined' || (typeof _localProgram.lastactivity_at !=='undefined' && _localProgram.lastactivity_at <=0)) {
        const lastActivity = program.exercises.reduce((carry, item) => {
          if (item.completed) {
            return Math.max(carry, item.completed_at)
          }
          return carry;
        }, 0);
        _localProgram.lastactivity_at = lastActivity;
      }
      if (toComplete === 0) {
        _localProgram.completed = true;
        _localProgram.overallperc = 100;
      } else {
        _localProgram.completed = false;
        _localProgram.overallperc = Math.round((exerciseCount - toComplete) / exerciseCount * 100);
      }
      state.joins[_localProgram.id] = {
        joined_at: joinTime,
        program: _localProgram,
        completed: completed,
        completed_at: completedTime
      }
      state.joinList.push(payload.id)
      state.updateTime = new Date().getTime();
      $nuxt.$emit("notification_schedule_program",_localProgram)
    }
  },
  SET_PROGRAM: (state, payload) => {
    const _localProgram = payload.localprogram
    const _remoteProgram = typeof payload.remoteprogram !== 'undefined' ? payload.remoteprogram : null
    let joinTime = new Date().getTime();
    let completedTime = null;
    let completed = false;
    if (_remoteProgram) {
      joinTime = new Date(_remoteProgram.joinedAt).getTime()
      completedTime = new Date(_remoteProgram.completedAt).getTime()
      completed = _remoteProgram.completed
    }
    if (typeof state.joins[_localProgram.id] !== 'undefined') {
      _localProgram.completed = completed;
      //Compute completed for program:
      const program = _localProgram
      const exerciseCount = program.exercises.length;
      const toComplete = program.exercises.reduce((carry, item) => {
        return carry + (item.completed ? 0 : 1);
      }, 0);
      if(typeof _localProgram.lastactivity_at == 'undefined' || (typeof _localProgram.lastactivity_at !=='undefined' && _localProgram.lastactivity_at <=0)) {
        const lastActivity = program.exercises.reduce((carry, item) => {
          if (item.completed) {
            return Math.max(carry, item.completed_at)
          }
          return carry;
        }, 0);
        _localProgram.lastactivity_at = lastActivity;
      }
      if (toComplete === 0) {
        _localProgram.completed = true;
        _localProgram.overallperc = 100;
      } else {
        _localProgram.completed = false;
        _localProgram.overallperc = Math.round((exerciseCount - toComplete) / exerciseCount * 100);
      }
      state.joins[_localProgram.id].program = _localProgram
      if (_remoteProgram) {
        state.joins[_localProgram.id].joined_at = joinTime
        state.joins[_localProgram.id].completed_at = completedTime
        state.joins[_localProgram.id].completed = completed
      }
      state.updateTime = new Date().getTime();
      $nuxt.$emit("notification_schedule_program",_localProgram)
    }
  },
  UPDATE_EXERCISE_PROGRAM: (state, payload) => {
    //payload == exercise
    if (typeof state.joins[payload.program_id] !== 'undefined') {
      const exerciseIndex = state.joins[payload.program_id].program.exercises.findIndex((item) => item.id === payload.id && item.program_index === item.program_index)
      if (exerciseIndex > -1) {
        Object.keys(payload).map((key) => {
          state.joins[payload.program_id].program.exercises[exerciseIndex][key] = payload[key]
        })
        state.updateTime = new Date().getTime();
      }
      //Compute completed for program:
      const program = state.joins[payload.program_id].program
      const exerciseCount = program.exercises.length;
      const toComplete = program.exercises.reduce((carry, item) => {
        return carry + (item.completed ? 0 : 1);
      }, 0);
      const lastActivity =program.exercises.reduce((carry, item) => {
        if(item.completed) {
          return Math.max(carry, item.completed_at)
        }
        return carry;
      }, 0);
      state.joins[payload.program_id].program.lastactivity_at = lastActivity;
      if (toComplete === 0) {
        state.joins[payload.program_id].program.completed = true;
        state.joins[payload.program_id].program.overallperc = 100;
      } else {
        state.joins[payload.program_id].program.completed = false;
        state.joins[payload.program_id].program.overallperc = Math.round((exerciseCount - toComplete) / exerciseCount * 100);
      }
    }
  },
  DEL_PROGRAM: (state, payload) => {
    if (typeof payload === 'string') {
      if (typeof state.joins[payload] !== 'undefined') {
        delete state.joins[payload]
      }
      const _index = state.joinList.indexOf(payload)
      if (_index > -1) {
        state.joinList.splice(_index, 1)
      }
      state.updateTime = new Date().getTime();
      return
    }
    if (typeof state.joins[payload.id] !== 'undefined') {
      delete state.joins[payload.id]
    } else {
      console.warn("Warning: program does not exist")
    }
    const _index = state.joinList.indexOf(payload.id)
    if (_index > -1) {
      state.joinList.splice(_index, 1)
    }
    state.updateTime = new Date().getTime();
  },
  CLEAR_ALL: (state, payload) => {
    state.joins = {};
    while (state.joinList.length > 0) {
      state.joinList.splice(0, 1)
    }
    state.updateTime = new Date().getTime();
  },
  CLEAR_ALL_SHOWN: (state, payload) => {
    while (state.shownPopups.length > 0) {
      state.shownPopups.splice(0, 1)
    }
    state.updateTime = new Date().getTime();
  },
  SET_SHOWNPOPUP: (state, payload) => {
    const key = payload.id + "_" + payload.tagId;
    state.shownPopups.push(key);
    state.updateTime = new Date().getTime();
  }
}
