

const errorImage = require("~/assets/images/errorlogo.png");
export default {
  props: {
    title: {
      type: String,
      default: "An error has occurred"
    },
    error: {
      type: String,
      default: "Ops try again later"
    },
    errorImage: {
      type: String,
      default: errorImage
    },
    show: {
      type: Boolean,
      default: false
    },
    hasretry : {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {}
  },
  computed: {
    showDialog: function () {
      return this.show;
    }
  },
  methods : {
    retryClick(e) {
      this.$emit('click',e);
    },
    resetClick(e) {
      this.$emit('close',e);
    }
  },
  mounted() {

  }
}
