export default {
  getVideoDevices: state => state.videodevices,
  getAudioDevices: state => state.audiodevices,
  getHomeScroll: state => state.homeScroll,
  getOriScroll: state => (id) => {
    if (id === 'mychallenges') {
      return state.oriMyChallengeScroll;
    } else if (id === 'exercises') {
      return state.oriExerciseScroll;
    } else if (id === 'challenges') {
      return state.oriChallengeScroll;
    }
    return null
  }
}
