export default {
  SET_CURRENTUSER: (state, payload) => {
    state.currentuser = payload
    let isloggedout = false;
    let isGuest = true;
    if (typeof state.currentuser.name === 'undefined') {
      isloggedout = true;
    } else if ($nuxt.$auth.loggedIn === false) {
      isloggedout = true;
    }
    if (typeof state.currentuser.isGuest !== 'undefined') {
      isGuest = state.currentuser.isGuest;
    }
    if (state.currentuser.password) {
      delete (state.currentuser.password)
    }
    if (state.currentuser.roles) {
      delete (state.currentuser.roles)
    }
    if (isloggedout) {
      $nuxt.$airlyn.setUnLoggedUser().then((r) => {
        console.log('unlogged user');
      })
      state.isguest = false;
      state.sessionCounter = 0;
      state.performed = [];
    } else {
      if (!isGuest) {
        $nuxt.$airlyn.setLoggedInUser(
          {
            "user_id": state.currentuser._id,
            "user_name": state.currentuser.name || state.currentuser.email,
            "user_email": state.currentuser.email
          }
        ).then((r) => {
          console.log('logged in user');
        })
      }
    }

  },
  SET_CURRENTUSERISGUEST: (state, payload) => {
    state.isguest = payload
    if (state.isguest) {
      state.sessionCounter = 0;
      state.skiptutorial = false
      $nuxt.$airlyn.setUnLoggedUser().then((r) => {
        console.log('unlogged user');
      })
      $nuxt.$airlyn.startTrial().then((r) => {
        console.log('trial started');
      })
    }
  },
  SET_CURRENTUSERVERIFICATIONCODE: (state, payload) => {
    state.currentuser.verificationcode = payload
  },
  SET_PERFORMEDEXERCISES: (state, payload) => {
    state.performed = payload
  },
  SET_SKIPTUTORIAL: (state, payload) => {
    state.skiptutorial = payload
  },
  ADD_SESSIONCOUNTER: (state, payload) => {
    state.sessionCounter += parseInt(payload)
  },
  SET_SESSIONCOUNTER: (state, payload) => {
    state.sessionCounter = parseInt(payload)
  },
  ENABLE_NOTIFICATIONS: (state, payload) => {
    state.notification.enabled = true;
    $nuxt.$emit("notification_settings_changed", state.notification)
  },
  DISABLE_NOTIFICATIONS: (state, payload) => {
    state.notification.enabled = false;
    state.notification.preferredtime = null;
    $nuxt.$emit("notification_settings_changed", state.notification)
  },
  SET_NOTIFICATIONS: (state, payload) => {
    state.notification.preferredtime = payload;
    $nuxt.$emit("notification_settings_changed", state.notification)
  },
}
