
export default {
  props: {
    'hidefooter': {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      showFooter: true,
      enableSwipte:false
    }
  },
  mounted() {
    this.showFooter = !this.hidefooter;
    if(this.$el.getAttribute("data-swipe-enable")==='true') {
      this.enableSwipte = true;
    }
  }
}
